import React, { useState } from "react"
import { Wrapper } from "./style"
import styled from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const Flex = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 100%;
  margin-bottom: 300px;
  text-align: center;
`

const Button = styled.button`
  max-width: 125px;
  max-height: 75px;

  min-width: 80px;
  width: 10vw;
  min-height: 50px;
  height: 7vw;

  background: #ffffff;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 42px;
  outline: none;
  border: none;
  position: absolute;
  bottom: 40px;

  :hover {
    cursor: pointer;
  }
`

const QuickTheory: any = () => {
  const [displayImage, setDisplayImage] = useState(false)
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "images/quick-sort.PNG" }) {
        childImageSharp {
          fixed(height: 500) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <Wrapper>
      <Flex>
        <div>
          <h1>Quick sort (Sortowanie szybkie)</h1>
          {displayImage ? (
            <>
              <Img
                fixed={data.file.childImageSharp.fixed}
                alt="Sortowanie szybkie"
              />
            </>
          ) : (
            <>
              <span>
                Algorytm sortowania szybkiego jest wydajny: jego rednia
                zoono obliczeniowa jest rzdu O(nlogn).
              </span>
              <br />
              <br />
              <span>
                Algorytm wykorzystuje technik "dziel i zwyciaj". Wedug
                ustalonego schematu wybierany jest jeden element w sortowanej
                tablicy, ktry bdziemy nazywa pivot. Pivot moe by elementem
                rodkowym, pierwszym, ostatnim, losowym lub wybranym wedug
                jakiego innego schematu dostosowanego do zbioru danych.
                Nastpnie ustawiamy elementy nie wiksze na lewo tej wartoci,
                natomiast nie mniejsze na prawo. W ten sposb powstan nam dwie
                czci tablicy (niekoniecznie rwne), gdzie w pierwszej czci
                znajduj si elementy nie wiksze od drugiej. Nastpnie kad z
                tych podtablic sortujemy osobno wedug tego samego schematu.
              </span>
            </>
          )}
        </div>
      </Flex>
      <Button onClick={() => setDisplayImage(old => !old)}>
        <b>{displayImage ? "Poka tre" : "Poka zdjcie"}</b>
      </Button>
    </Wrapper>
  )
}

export default QuickTheory
