import React, { useState } from "react"
import { Wrapper } from "./style"
import styled from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const Flex = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 100%;
  margin-bottom: 300px;
  text-align: center;
`

const Button = styled.button`
  max-width: 125px;
  max-height: 75px;

  min-width: 80px;
  width: 10vw;
  min-height: 50px;
  height: 7vw;

  background: #ffffff;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 42px;
  outline: none;
  border: none;
  position: absolute;
  bottom: 40px;

  :hover {
    cursor: pointer;
  }
`

const InsertionTheory: any = () => {
  const [displayImage, setDisplayImage] = useState(false)
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "images/insertion-sort.PNG" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)
  return (
    <Wrapper>
      <Flex>
        <div>
          <h1>Insertion sort (Sortowanie przez wstawianie)</h1>
          {displayImage ? (
            <Img
              fixed={data.file.childImageSharp.fixed}
              alt="Sortowanie przez wstawianie"
            />
          ) : (
            <>
              <span>
                Sortowanie przez wstawianie jest efektywne dla niewielkiej
                liczby elementw, jego zoono wynosi O(n^2).
              </span>
              <br />
              <span>
                Zasada dziaania odzwierciedla sposb w jaki ludzie ustawiaj
                karty  kolejne elementy wejciowe s ustawiane na odpowiednie
                miejsca docelowe.
              </span>{" "}
              <br />
              <span>
                Schemat dziaania algorytmu:
                <ol>
                  <li>
                    Utwrz zbir elementw posortowanych i przenie do niego
                    dowolny element ze zbioru nieposortowanego.
                  </li>
                  <li>We dowolny element ze zbioru nieposortowanego.</li>
                  <li>
                    Wycignity element porwnuj z kolejnymi elementami zbioru
                    posortowanego, pki nie napotkasz elementu rwnego lub
                    elementu wikszego (jeli chcemy otrzyma cig niemalejcy)
                    lub nie znajdziemy si na pocztku/kocu zbioru
                    uporzdkowanego.
                  </li>
                  <li>
                    Wycignity element wstaw w miejsce, gdzie skoczye
                    porwnywa.
                  </li>
                  <li>
                    Jeli zbir elementw nieuporzdkowanych jest niepusty, wr
                    do punktu 2.
                  </li>
                </ol>
              </span>
            </>
          )}
        </div>
      </Flex>
      <Button onClick={() => setDisplayImage(old => !old)}>
        <b>{displayImage ? "Poka tre" : "Poka zdjcie"}</b>
      </Button>
    </Wrapper>
  )
}

export default InsertionTheory
